h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

.is-hidden {
  display: none;
}

@media only screen and (max-width: 1199px) {
  .desk-only {
    display: none;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

button {
  background-color: transparent;
}

a:hover, a:focus {
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

table {
  vertical-align: top;
}

img {
  max-width: 100%;
  vertical-align: top;
}

* {
  -webkit-tap-highlight-color: transparent;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.footer {
  background-color: #f2f5f7;
}

.footer li:not(:last-child) {
  margin-bottom: 5px;
}

.footer__container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 45px 15px 30px;
}

@media only screen and (max-width: 767px) {
  .footer__container {
    flex-direction: column;
    align-items: center;
    padding: 35px 15px 30px;
  }
}

.footer__container ul a {
  transition: all 0.25s ease-out;
}

.footer__container ul a:hover {
  color: #249bff;
}

.footer__logo {
  flex: 0 0 196px;
  margin-top: 7px;
}

@media only screen and (max-width: 1199px) {
  .footer__logo {
    flex: 0 0 160px;
  }
}

@media only screen and (max-width: 767px) {
  .footer__logo {
    flex: 0 1 auto;
    width: 140px;
    margin-bottom: 25px;
  }
}

.footer__products {
  flex: 0 0 270px;
  margin: 0 70px 0 120px;
  column-count: 2;
  column-gap: 40px;
}

@media only screen and (max-width: 1199px) {
  .footer__products {
    margin: 0;
  }
}

@media only screen and (max-width: 767px) {
  .footer__products {
    display: none;
  }
}

.footer__nav {
  flex: 0 0 220px;
}

@media only screen and (max-width: 1199px) {
  .footer__nav {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .footer__nav {
    display: block;
    flex: 0 1 auto;
    margin-bottom: 25px;
  }
}

.footer__info {
  padding: 15px 0;
  background-color: #dbe1e6;
  font-size: 15px;
  line-height: 20px;
  color: #7a7a7a;
}

@media only screen and (max-width: 767px) {
  .footer__info {
    text-align: center;
  }
}

.footer__info a {
  border-bottom: 1px solid rgba(122, 122, 122, 0.8);
  transition: all 0.25s ease-out;
}

.footer__info a:hover {
  border-bottom-color: transparent;
}

.footer__info-container {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1199px) {
  .footer__info-container {
    flex-direction: column;
    align-items: center;
  }
}

.footer__info-container > p {
  margin-right: 115px;
}

@media only screen and (max-width: 1199px) {
  .footer__info-container > p {
    margin-right: 0;
  }
}

@media only screen and (max-width: 1199px) {
  .footer__info-container > * {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 767px) {
  .footer__info-container > a {
    order: -1;
  }
}

.footer__dev {
  margin-left: auto;
}

@media only screen and (max-width: 1199px) {
  .footer__dev {
    margin: 0;
    order: 1;
  }
}

.footer__basket {
  margin-right: 50px;
}

@media only screen and (max-width: 1199px) {
  .footer__basket {
    margin-right: 0;
  }
}
