@import "helpers/all";

.footer {
   background-color: #f2f5f7;
   li {
      &:not(:last-child) {
         margin-bottom: 5px; } }
   &__container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      padding: 45px 15px 30px;
      @include sm {
         flex-direction: column;
         align-items: center;
         padding: 35px 15px 30px; }
      ul {
         a {
            @include trs;
            &:hover {
               color: $color3; } } } }
   &__logo {
      flex: 0 0 196px;
      margin-top: 7px;
      @include md {
         flex: 0 0 160px; }
      @include sm {
         flex: 0 1 auto;
         width: 140px;
         margin-bottom: 25px; } }
   &__products {
      flex: 0 0 270px;
      margin: 0 70px 0 120px;
      column-count: 2;
      column-gap: 40px;
      @include md {
         margin: 0; }
      @include sm {
         display: none; } }
   &__nav {
      flex: 0 0 220px;
      @include md {
         display: none; }
      @include sm {
         display: block;
         flex: 0 1 auto;
         margin-bottom: 25px; } }

   &__info {
      padding: 15px 0;
      background-color: #dbe1e6;
      font-size: 15px;
      line-height: 20px;
      color: $color2;
      @include sm {
         text-align: center; }
      a {
         border-bottom: 1px solid rgba($color2, .8);
         @include trs;
         &:hover {
            border-bottom-color: transparent; } }
      &-container {
         display: flex;
         align-items: center;
         @include md {
            flex-direction: column;
            align-items: center; }
         > p {
            margin-right: 115px;
            @include md {
               margin-right: 0; } }
         > * {
            @include md {
               margin-bottom: 10px; } }
         > a {
            @include sm {
               order: -1; } } } }
   &__dev {
      margin-left: auto;
      @include md {
         margin: 0;
         order: 1; } }
   &__basket {
      margin-right: 50px;
      @include md {
         margin-right: 0; } } }
